<template>
  <transition name="transition-fade">
    <div class="modal-abstraction" @click.stop="clickOutside">
      <div class="modal-abstraction__content">
        <slot />
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'ModalAbstraction',

  mounted() {
    document.body.style.overflowY = 'hidden'
  },

  beforeDestroy() {
    document.body.style.overflowY = 'auto'
  },

  methods: {
    clickOutside(e) {
      if (e.target?.closest('.modal-abstraction__content')) return
      this.$emit('close')
    },
  },
}
</script>

<style lang="scss" scoped>
.modal-abstraction {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 100;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  animation: appear $default-transition;
}
</style>
